body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  height: 100%;
  overflow: hidden;
}

.App {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  background: #1d2531;
  height: auto;
  display: flex;
  flex-direction: column;
}

.hidden {
  display: none;
}

.visible {
  display: block;
}

.landing {
  height: 100vh;
  width: 100%;
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.circle1 {
  position: fixed;
  height: 500px;
  width: 500px;
  background: #d83f87;
  opacity: 0.2;
  border-radius: 50%;
  bottom: -300px;
  left: -300px;
}

.circle2 {
  position: fixed;
  height: 500px;
  width: 500px;
  background: #364962;
  opacity: 0.2;
  border-radius: 50%;
  top: -300px;
  right: 0;
}

.circle3 {
  position: fixed;
  height: 200px;
  width: 200px;
  background: #b66565;
  border-radius: 50%;
  opacity: 0.2;
  left: 100px;
  top: 225px;
}

.left {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.headingBlock {
  color: white;
  align-items: left;
  margin: auto;
}

.headingBlock h1 {
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1em;
  font-size: 70px;
  margin-bottom: -1.5%;
  text-transform: uppercase;
}

.headingBlock span {
  font-family: 'Assistant';
  font-size: 30px;
  text-transform: lowercase;
}

.me img {
  position: absolute;
  max-height: 80vh;
  max-width: 100%;
  right: 0;
  bottom: 0;
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.icon {
  font-size: 40px;
}

.icon:hover {
  color: #ff8c8c;
  cursor: pointer;
  transform: scale(1.2);
  transition: 0.4s;
}

p {
  font-family: 'Assistant';
  max-width: 40%;
}

.buttons {
  max-width: 40%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.icon:hover {
  color: '#ff8c8c';
  cursor: 'pointer';
  scale: 1.2;
}

a {
  color: white;
  text-decoration: none;
}

@media (max-width: 992px) {
  .me {
    visibility: hidden;
  }

  .circle3 {
    display: none;
  }

  .headingBlock {
    text-align: center;
  }

  p {
    max-width: 100%;
  }

  .buttons {
    max-width: 100%;
    justify-content: center;
  }
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.projects {
  height: auto;
  width: 100%;
  background: #1d2531;
}

.headingBlock h1 {
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1em;
  font-size: 50px;
  margin-bottom: -1.5%;
  text-transform: uppercase;
}

.project {
  position: relative;
  max-width: 500px;
  max-height: 700px;
}

.projects img {
  position: relative;
  max-width: 100%;
  max-height: 100%;
}

.lead {
  letter-spacing: 0.2rem;
}

.project:hover .pImg {
  cursor: pointer;
  -webkit-filter: brightness(0.3);
          filter: brightness(0.3);
  transition: 0.5s all;
}

.content {
  position: absolute;
  top: 0;
  left: 0;
  color: white;
}

.learn {
  display: none;
}

.project:hover .learn {
  display: block;
  transition: 0.5s all;
  display: flex;
  flex-direction: row;
}

.rightArrow {
  font-size: 30px;
  -webkit-animation: blinker 1s linear infinite;
          animation: blinker 1s linear infinite;
}

.content h4 {
  font-family: 'Roboto', Sans Serif;
  font-size: 2.2rem;
  margin-bottom: -1.5%;
}

@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

@media (min-width: 641px) and (max-width: 992px) {
  .content h4 {
    font-size: 1.5rem;
  }
}

